<template>
    <div class="fs-12 lad-lister" v-if="me !=null">
        <div class="qiong-td">
            <div class="qiong-wide-47 pr-0">
                用戶編號：{{ me.number_code }}
            </div>
            <div class="qiong-wide-60 pr-0">
                支付級別：<level-viewing :item="me.vip.named + '_' + me.vip.star" class="d-inline"></level-viewing>
            </div>
        </div>
        <div class="">
            用戶全稱：{{ $store.state.user_backend.display_name }}
        </div>
        <div class="qiong-menu-sub">
            IPv：{{ me.ipv }}
        </div>
    </div>

    <div v-else class="Lad_Three_Line pt-9">
        <qiong-loading :bigger="-1"></qiong-loading>
    </div>
</template>

<script>
import QiongLoading from '../../Qiong/Ui/QiongLoading.vue'
import LevelViewing from '../../Viewing/LevelViewing.vue'
    export default {
  components: { 
        LevelViewing,
        QiongLoading          
    },
        props: {
            me: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
.Lad_Three_Line
    min-height: 80px
</style>


