<template>
        <!-- v-icon size="16" color="rgba(168, 255, 255, 0.56">edit</v-icon -->
        <v-navigation-drawer
            v-model="drawer"
            width="270"
            app

            dark
            :mini-variant.sync="mini"
            mini-variant-width="68"
            
            enable-resize-watcher
            floating
            permanent

            class="right-shadow appBar_dark qiong-pt-20"
        >
            <v-list nav dense class="px-0">
                <v-list-item-group>
                    <v-list-item class="flex-center" @click="userCenter">
                        <v-list-item-avatar size="90" class="ma-0">
                            <v-img :src="face"></v-img>
                        </v-list-item-avatar>
                    </v-list-item>
                    <v-list-item class="flex-center" @click="userCenter">
                        <v-list-item-content class="qiong-pt-5">
                            <v-list-item-subtitle 
                                class="text-center over-view pb-2 pt-1"
                                v-if="$store.state.user_backend" 
                                >
                                <div class="fs-18">{{ $store.state.user_backend.username }}</div>
                            </v-list-item-subtitle>
                            <v-list-item-subtitle v-else>
                                Admin
                            </v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                </v-list-item-group>


                <lad-msg class="qiong-pt-5 mt-1 pl-4" :me="mine"></lad-msg>
                
                <qiong-gradient-line class="qiong-pt-5 mt-1"></qiong-gradient-line>

                <lad-bonus class="qiong-pt-10 mt-1 pl-4" :me="mine"></lad-bonus>

                <!-- 按键列表 -->
                <lad-lister class="qiong-pt-15 mt-2"></lad-lister>

            </v-list>


            <v-spacer></v-spacer>

        </v-navigation-drawer>
</template>

<script>
import QiongGradientLine from '../Qiong/Ui/QiongGradientLine.vue'
import LadBonus from './Inner/LadBonus.vue'
import LadLister from './Inner/LadLister.vue'
import LadMsg from './Inner/LadMsg.vue'
    export default {
        name: '',
        inject: [ 'reload' ],
        components: {

            LadLister,
                QiongGradientLine,
                LadMsg,
                LadBonus
        },
        data() {
            return {
                face: require('../../assets/FACE.jpg'),
                drawer: false,
                group2: null,
                mini: false,

                mine: null
            }
        },
        watch: {
            group() {
                this.drawer = false
            },
        },
        mounted() {
            this.me()

        },
        methods: {
            changeF() {
                this.drawer = !this.drawer
            },
            init() {
                this.me()
            },
            me(sort = true) {
                const _this = this

                const id = this.$store.state.user_backend.unid
                const many = this.$store.state.users_sponser
                const transfer = this.$store.state.transfer

                if (many) {

                    this.bonus._build_res(many, transfer, function (u, t) {

                        if (u.unid == id) {

                            _this.mine = JSON.parse(JSON.stringify(u))
                        }
                    })

                    // this.reload()
                }
            },

            userCenter() {
                
                this.$router.push({
                    path: '/home/user/center' })
            }
        }
    }
</script>

<style lang="sass" scoped>
    .right-shadow
        box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.14)

    .last-nav
        position: absolute
        bottom: 0
        width: 100%
</style>


